@use 'base/variables' as  v;
@use 'base/mixins' as  m;

.trabajos {
    width: 90%;
    margin: 0 auto;
    &__grid {
        display: flex;
        flex-direction: column;
        @include m.tablet {
            @include m.grid(2, 2rem);
        }
    }
    &__destacados,
    &__recientes{
        display: flex;
        flex-direction: column;
        gap: 2rem;
    }
    &__desPri,
    &__recPri {
        max-height: 100rem;
    }
    &__desPri {
        display: flex;
        flex-direction: column;
        gap: 2rem;
    }
    &__recPri {
        display: flex;
        flex-direction: column;
        gap: 2rem;
        @include m.tablet {
            border: 2px solid v.$gris;
        }
        padding: 1rem;
        overflow: hidden;
    }
    &__h2 {
        color: v.$letras;
        margin: 2rem auto 0 auto;
        @include m.tablet {
            margin: 0 auto;
        }
        padding: 0.4rem;
        width: 20rem;
        font-size: 2.6rem;
        text-align: center;
        border-bottom: 2px solid v.$primario;
    }
    &__desBox {
        background-image: url("../img/proyectos/juanbarber.avif");
        background-position: center 0;
        background-size: 100%;
        background-repeat: no-repeat;
        width: cover;
        height: 20rem;
        @include m.tablet {
            height: 33.33rem;
        }
        border-radius: 1rem;
    }
    &__recBox {
        border-bottom: 2px dashed v.$primario;
        &:last-child {
            border: 0;
        }
        padding: 1rem;
        display: flex;
        flex-direction: column;
        height: 25rem;
    }
    &__titulo {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
    }
    &__imagen {
        width: 6rem;
        height: 6rem;
        background-color: v.$blanco;
        background-image: url("../img/logos/icono-jb.avif");
        background-position: center;
        background-size: 80%;
        background-repeat: no-repeat;
        border-radius: 100%;
        border: 2px solid v.$gris;
    }
    // &__informacion {
    //     // Nada
    // }
    &__h3 {
        color: v.$letras;
        font-size: 2rem;
        line-height: 1;
        text-transform: capitalize;
        margin: 0 0 4px 0;
    }
    &__version {
        color: v.$letras;
        font-size: 1.2rem;
        display: block;
    }
    &__detalles {
        color: v.$letras;
        height: 5rem;
        text-align: center;
    }
    &__boton {
        @include m.boton(v.$colorBoton, v.$letras);
        font-size: 1.6rem;
        margin: 0 auto;
        width: 100%;
        @include m.tablet {
        }
    }
    &__recientes {
        display: flex;
        flex-direction: column;
        gap: 2rem;
    }
}