/** Media Queries **/
.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #1c1c1c;
}

.pad {
  padding: 3rem 0 2rem 0;
}

[class*=__header] {
  margin: 4rem 0 4rem 0;
}

[class*=__titulo-pri] {
  color: #FFFFFF;
  text-transform: capitalize;
  line-height: 0.8;
  color: #FFFFFF;
  font-weight: 400;
}

[class*=__span-tit] {
  color: #2B2B2B;
  font-weight: 700;
}

[class*=__span-min] {
  color: #1c1c1c;
  display: block;
  font-size: 2rem;
  font-weight: 700;
}

.non {
  display: none;
}

.espaciado {
  padding: 2.5rem;
}

h1, h2, h3 {
  font-family: "Roboto", sans-serif;
  margin: 0 0 5rem 0;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.5;
}

h1 {
  font-size: 3.5rem;
  margin: 0;
}
@media (min-width: 768px) {
  h1 {
    font-size: 4rem;
  }
}
@media (min-width: 1024px) {
  h1 {
    font-size: 4.5rem;
  }
}

h2 {
  font-size: 2.6rem;
  margin: 0.2rem;
  color: #1c1c1c;
  line-height: 1;
  text-transform: capitalize;
}
@media (min-width: 768px) {
  h2 {
    font-size: 3rem;
  }
}
@media (min-width: 1024px) {
  h2 {
    font-size: 3.5rem;
  }
}
h2 span {
  color: #daddd8;
  font-size: 1.2rem;
  display: block;
}

h3 {
  font-size: 2.6rem;
}
@media (min-width: 768px) {
  h3 {
    font-size: 2.8rem;
  }
}
@media (min-width: 1024px) {
  h3 {
    font-size: 3rem;
  }
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  background-color: #ffffff;
}

p {
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  p {
    font-size: 1.8rem;
  }
}
@media (min-width: 1024px) {
  p {
    font-size: 2rem;
  }
}

.contenedor {
  width: 90%;
  margin: 0 auto;
}
@media (min-width: 480px) {
  .contenedor {
    max-width: 40rem;
  }
}
@media (min-width: 580px) {
  .contenedor {
    max-width: 50rem;
  }
}
@media (min-width: 768px) {
  .contenedor {
    max-width: 60rem;
  }
}
@media (min-width: 868px) {
  .contenedor {
    max-width: 70rem;
  }
}
@media (min-width: 1024px) {
  .contenedor {
    max-width: 100rem;
  }
}
@media (min-width: 1224px) {
  .contenedor {
    max-width: 120rem;
  }
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

.header {
  background-color: #ffffff;
  padding: 2rem;
  margin: 0;
}
.header__grid {
  display: flex;
  gap: 2rem;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .header__grid {
    flex-direction: row;
    justify-content: space-around;
  }
}
.header__nav__pri, .header__nav__sec {
  display: none;
}
@media (min-width: 768px) {
  .header__nav__pri, .header__nav__sec {
    padding: 0.6rem 1rem;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    gap: 5rem;
  }
}
@media (min-width: 768px) {
  .header__nav__pri {
    border-right: 2px solid #daddd8;
  }
}
@media (min-width: 768px) {
  .header__nav__sec {
    border-left: 2px solid #daddd8;
  }
}
.header__link {
  text-transform: uppercase;
  color: #1c1c1c;
  font-size: 1.6rem;
  font-weight: 700;
  padding: 0.6rem;
  transition-property: border;
  transition-duration: 0.3s;
  border-top: 4px solid #ffffff;
  border-bottom: 4px solid #ffffff;
}
.header__link:hover {
  border-bottom-color: #daddd8;
}
.header__img__perfil {
  background-color: #daddd8;
  background-image: url("../img/foto_perfil.webp");
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center -1rem;
  width: 10rem;
  height: 10rem;
  border: 4px solid #daddd8;
  border-radius: 100%;
  order: -1;
}
@media (min-width: 768px) {
  .header__img__perfil {
    order: 0;
  }
}

.bienvenida {
  background-color: #1c1c1c;
  margin-top: -7rem;
  padding: 4rem 2rem;
}
@media (min-width: 768px) {
  .bienvenida {
    margin: 0;
    height: auto;
    justify-content: space-between;
  }
}
.bienvenida__grid {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  justify-content: space-evenly;
  height: 30rem;
}
.bienvenida__h1 {
  color: #FC7643;
  font-size: 3rem;
  width: 30rem;
  margin: 0 auto;
  padding-top: 1rem;
}
@media (min-width: 768px) {
  .bienvenida__h1 {
    padding-top: 0;
  }
}
.bienvenida__span {
  color: #ffffff;
  display: block;
  font-size: 1.2rem;
  margin-top: -10px;
  text-transform: capitalize;
  border-bottom: 2px solid #daddd8;
}
.bienvenida__descripcion {
  text-align: center;
}
.bienvenida__texto {
  color: #ffffff;
  width: 90%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .bienvenida__texto {
    width: 50%;
  }
}
.bienvenida__boton {
  background-color: #daddd8;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #1c1c1c;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  margin: 0 auto;
  text-transform: uppercase;
}
.bienvenida__boton:hover {
  cursor: pointer;
  background-color: #c0c5bd;
}
@media (min-width: 768px) {
  .bienvenida__boton {
    width: 30rem;
  }
}

.trabajos {
  width: 90%;
  margin: 0 auto;
}
.trabajos__grid {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .trabajos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.trabajos__destacados, .trabajos__recientes {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.trabajos__desPri, .trabajos__recPri {
  max-height: 100rem;
}
.trabajos__desPri {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.trabajos__recPri {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 1rem;
  overflow: hidden;
}
@media (min-width: 768px) {
  .trabajos__recPri {
    border: 2px solid #484349;
  }
}
.trabajos__h2 {
  color: #1c1c1c;
  margin: 2rem auto 0 auto;
  padding: 0.4rem;
  width: 20rem;
  font-size: 2.6rem;
  text-align: center;
  border-bottom: 2px solid #1c1c1c;
}
@media (min-width: 768px) {
  .trabajos__h2 {
    margin: 0 auto;
  }
}
.trabajos__desBox {
  background-image: url("../img/proyectos/juanbarber.avif");
  background-position: center 0;
  background-size: 100%;
  background-repeat: no-repeat;
  width: cover;
  height: 20rem;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .trabajos__desBox {
    height: 33.33rem;
  }
}
.trabajos__recBox {
  border-bottom: 2px dashed #1c1c1c;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  height: 25rem;
}
.trabajos__recBox:last-child {
  border: 0;
}
.trabajos__titulo {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.trabajos__imagen {
  width: 6rem;
  height: 6rem;
  background-color: #FFFFFF;
  background-image: url("../img/logos/icono-jb.avif");
  background-position: center;
  background-size: 80%;
  background-repeat: no-repeat;
  border-radius: 100%;
  border: 2px solid #484349;
}
.trabajos__h3 {
  color: #1c1c1c;
  font-size: 2rem;
  line-height: 1;
  text-transform: capitalize;
  margin: 0 0 4px 0;
}
.trabajos__version {
  color: #1c1c1c;
  font-size: 1.2rem;
  display: block;
}
.trabajos__detalles {
  color: #1c1c1c;
  height: 5rem;
  text-align: center;
}
.trabajos__boton {
  background-color: #eef0f2;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #1c1c1c;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  font-size: 1.6rem;
  margin: 0 auto;
  width: 100%;
}
.trabajos__boton:hover {
  cursor: pointer;
  background-color: #d1d7dc;
}
@media (min-width: 768px) {
  .trabajos__boton {
    width: 30rem;
  }
}
.trabajos__recientes {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.recientes {
  width: 90%;
  margin: 0 auto;
}
.recientes__grid {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 768px) {
  .recientes__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.recientes__contenedor {
  border: 2px solid #1c1c1c;
  max-height: 30rem;
}
.recientes__imagen {
  height: 14rem;
  width: 100%;
  margin: 0 auto;
}
.recientes__informacion {
  background-color: #1c1c1c;
  padding: 6px 2rem;
}
.recientes__h3 {
  color: #ffffff;
  font-size: 2rem;
  line-height: 1;
  text-transform: capitalize;
  padding: 1rem 0 4px 0;
  margin: 0;
}
.recientes__version, .recientes__fecha {
  color: #FC7643;
  font-size: 1.2rem;
  text-align: center;
  display: block;
}
.recientes__texto {
  color: #daddd8;
  margin: 0;
  padding: 1rem;
  text-align: center;
  font-size: 1.7rem;
}